@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Poppins", sans-serif}body{background-color:#ebeef5}.dashboard{background-color:#f4f5f7;display:grid;grid-template-columns:[sidebar-start] minmax(13rem, 0.3fr) [sidebar-end navbar-start] minmax(4rem, 1fr) [navbar-end];grid-template-rows:3.5rem calc(100vh - 3.5rem)}.Header{grid-column:navbar-start / navbar-end;grid-row:1 / 2;padding:0 1rem}.header-inner{display:flex;align-items:center;justify-content:space-between;background-color:#0364ff;width:100%;height:100%;color:white;padding:0.1rem 0.5rem;border-bottom-left-radius:10px;border-bottom-right-radius:10px}.header-inner p{font-size:24px}.header-inner svg{color:white}.Editor{grid-column:navbar-start / navbar-end;padding:1rem}.editor-inner{border-radius:15px;background-color:white;height:100%;padding:0.5rem}.editor-inner textarea{width:100%;outline:none;border:none;background:transparent;margin-top:0.5rem;height:calc(100% - 13.5rem);white-space:pre-line}.editor-inner textarea::-webkit-scrollbar{width:5px}.editor-inner textarea::-webkit-scrollbar-track{background-color:rgba(0,0,0,0.2);border-radius:5px}.editor-inner textarea::-webkit-scrollbar-thumb{background-color:#0364ff;border-radius:5px}.DropdownFont{display:inline-block}.dropdown-container{display:flex;align-items:center;justify-content:space-between}.preview-img{width:100%;height:100%;object-fit:contain}.center-text{display:flex;align-items:center;grid-gap:0.4rem;gap:0.4rem}input.title-input{border:none;outline:none;border-bottom:1px solid black;font-size:16px;font-weight:800;padding:0 0.2rem}.center-text{display:none}.add-image{background-color:rgba(0,0,0,0.2);border-radius:10px;height:10rem;width:100%;transition:all 0.3s ease;cursor:pointer;margin-bottom:0.5rem}.add-image label{display:flex;align-items:center;justify-content:center;width:100%;height:100%;cursor:pointer;position:relative}.add-image:hover{background-color:rgba(0,0,0,0.7)}.add-image:hover .intial-text{color:white}.add-image:hover .center-text{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);z-index:19;display:flex;background-color:#0364ff;padding:0.4rem 0.8rem;border-radius:30px;color:white}button.save{border-radius:20px;padding:0.5rem 1rem;cursor:pointer;color:white;display:flex;align-items:center;border:none;outline:none;box-shadow:0 3px 16px -5px black;transition:all 0.2s ease}button.save svg{margin-left:10px}.bg-blue{background-color:#0364ff}.bg-green{background-color:#38a3a5}.bottom-container{position:absolute;bottom:5%;right:3%;display:flex;grid-gap:1rem;gap:1rem}.Sidebar{grid-row:1 / -1;background-color:#00315c;overflow:hidden}.sidebar-inner{background-color:#00315c;height:calc(100% - 60px);padding:0.4rem;overflow-y:scroll}.sidebar-inner::-webkit-scrollbar{width:3px}.note-card{background-color:white;border-radius:3px;font-weight:700;box-shadow:0 1px 12px -4px white;overflow:hidden;margin-bottom:1rem;cursor:pointer}.note-card .note-text{font-size:14px;font-weight:400;padding:0.2rem}.note-card .note-footer{padding:0.3rem;max-height:10rem}.note-card:hover .delete-ico{opacity:1}.loadingPosition{width:100%;height:calc(100% - 60px);display:flex;justify-content:center;align-items:center}.search{width:100%;border-radius:3px;background-color:#002140;display:flex;align-items:center;padding:0.5rem;margin-top:0.5rem;margin-bottom:1rem;box-shadow:0 1px 1px 1px;box-shadow:0 1px 11px -5px;color:white}.search svg{fill:#002140}.search input{border:none;outline:none;background:transparent;flex:1 1;padding:0 0.3rem;color:white}.search input::-webkit-input-placeholder{color:white}.search input:-ms-input-placeholder{color:white}.search input::-ms-input-placeholder{color:white}.search input::placeholder{color:white}.note-text-card{background-color:white;padding:0.5rem}.note-text-card h4{font-size:20px}.note-text-card p{font-weight:400;font-size:12px}.note-image-container{position:relative}.note-image-container p{position:absolute;bottom:3%;left:2%;color:white;font-weight:300;font-size:11px}.note-image-container h3.title{position:absolute;bottom:16%;left:2%;color:white;font-size:22px}.flex-align{display:flex;align-items:center}.login-container{display:flex;align-items:center;height:100vh;padding:2rem}.login-container .illus svg{width:90%}.login-container .login-form{width:50%}.login-container .login-form-inner{padding:1rem;background-color:white;display:flex;flex-direction:column;grid-gap:1rem;gap:1rem;border-radius:10px;width:25rem;color:#0364ff}.login-container .login-form-inner input{border:none;outline:none;background-color:#ebeef5;padding:0.5rem 0.5rem;border-radius:10px}.submit-btn{background-color:#0364ff;border:none;outline:none;cursor:pointer;display:flex;align-items:center;justify-content:center;padding:0.5rem;border-radius:10px;color:white}.no-account{text-align:center;color:#0364ff;text-decoration:underline}

